@import url(https://fonts.googleapis.com/css?family=Titan+One&display=swap);
*, *::after, *::before {
  font-family: 'Titan One', cursive;
  box-sizing: border-box; }

body {
  margin: 0;
  font-size: 16px; }
  @media (min-width: 1000px) {
    body {
      overflow: hidden; } }

.App {
  min-height: 100vh;
  width: 100%;
  display: grid;
  justify-items: center;
  align-items: center; }
  .App .show-modal {
    position: fixed;
    left: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    padding: 15px 0;
    width: 12em;
    border: 0;
    background-color: #FCB639;
    border-right: 8px solid #c69422;
    cursor: pointer;
    font-size: 1.2em;
    -webkit-transition: .3s;
    transition: .3s; }
    .App .show-modal:hover {
      width: 11.5em; }
  @media (max-width: 1100px) {
    .App {
      font-size: 15px; } }
  @media (max-width: 1000px) {
    .App {
      font-size: 13px; } }
  @media (max-width: 850px) {
    .App {
      font-size: 11px; } }

.modal {
  position: relative;
  min-width: 730px;
  max-width: 980px;
  width: 90%;
  -webkit-transition: .7s;
  transition: .7s; }
  .modal header {
    position: absolute;
    left: 50%;
    top: -8%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 42%; }
    .modal header h2 {
      position: absolute;
      top: 33%;
      width: 100%;
      margin: 0;
      text-align: center;
      font-size: 2.25em;
      text-shadow: 3px 3px #a4a3a1; }
  .modal-content {
    position: absolute;
    left: 50%;
    top: 6%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    min-width: 50%;
    opacity: 1;
    display: grid;
    justify-items: center; }
    .modal-content__price {
      display: grid;
      grid-template-columns: auto -webkit-max-content auto;
      grid-template-columns: auto max-content auto;
      align-items: center; }
      .modal-content__price h1 {
        font-size: 6em;
        color: white;
        text-align: center;
        padding: 0 20px;
        margin-bottom: 50px; }
      .modal-content__price svg {
        -webkit-transform: translateY(25px);
                transform: translateY(25px); }
    .modal-content__payment {
      width: 100%;
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-column-gap: 40px;
      text-align: center; }
      .modal-content__payment .modal-content__btn {
        padding: 5px 10px;
        border: solid 4px black;
        background-color: white;
        cursor: pointer; }
        @media (max-width: 1000px) {
          .modal-content__payment .modal-content__btn {
            max-height: 80px; } }
        .modal-content__payment .modal-content__btn--paypal, .modal-content__payment .modal-content__btn--credit-card {
          position: relative;
          -webkit-transition: .3s;
          transition: .3s; }
          .modal-content__payment .modal-content__btn--paypal .gwiazdka, .modal-content__payment .modal-content__btn--credit-card .gwiazdka {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 145px;
            height: 145px;
            z-index: -1;
            -webkit-animation: spin 20s infinite linear alternate;
                    animation: spin 20s infinite linear alternate; }

@-webkit-keyframes spin {
  from {
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(0);
            transform: translateX(-50%) translateY(-50%) rotate(0); }
  to {
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(180deg);
            transform: translateX(-50%) translateY(-50%) rotate(180deg); } }

@keyframes spin {
  from {
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(0);
            transform: translateX(-50%) translateY(-50%) rotate(0); }
  to {
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(180deg);
            transform: translateX(-50%) translateY(-50%) rotate(180deg); } }
          .modal-content__payment .modal-content__btn--paypal img, .modal-content__payment .modal-content__btn--paypal h3, .modal-content__payment .modal-content__btn--credit-card img, .modal-content__payment .modal-content__btn--credit-card h3 {
            position: relative; }
        .modal-content__payment .modal-content__btn--paypal img {
          width: 60%; }
        .modal-content__payment .modal-content__btn--credit-card {
          position: relative;
          display: grid;
          grid-template-columns: minmax(40px, 80px) auto;
          grid-gap: 10px;
          align-items: center; }
          .modal-content__payment .modal-content__btn--credit-card img {
            width: 100%; }
          .modal-content__payment .modal-content__btn--credit-card h3 {
            font-size: 1.5em;
            margin: 0;
            text-align: left; }
            .modal-content__payment .modal-content__btn--credit-card h3::after {
              content: '';
              position: absolute;
              top: .55em;
              right: 11%;
              width: 40%;
              height: 6px;
              border-radius: 10px;
              background-color: #ff5f64; }
  .modal__pay-button {
    position: absolute;
    left: 50%;
    bottom: -3%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 30%;
    text-align: center;
    background-color: #FCB639;
    border: 8px solid #c69422;
    cursor: pointer;
    -webkit-transition: .2s;
    transition: .2s;
    -webkit-transform-origin: center;
            transform-origin: center; }
    .modal__pay-button:hover {
      width: 32%; }
    .modal__pay-button h3 {
      font-size: 1.9em;
      margin: 5px 0;
      letter-spacing: 2px; }

